import { mapState } from "vuex";

export default {
    data() {
        return {
            express: '',
            id: '',
            express_no: '',
            express_company: ''
        }
    },
    computed: {
        ...mapState({
            userInfo: state => state.user.info,
            adminImg: state => state.mallConfig.__wxapp_img.app_admin,
        })
    },
    methods: {
        copy() {
            this.$utils.uniCopy({
                data: this.express_no,
                success() {
                    uni.getClipboardData();
                }
            })
        },
    },

    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        that.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        that.$request({
            url: that.$api.app_admin.express_detail,
            data: {
                id: options.id,
                express: options.express,
                customer_name: options.customer_name,
                express_no: options.express_no
            }
        }).then(response => {
            that.$hideLoading();
            if (response.code == 0) {
                that.express = response.data.express;
                that.id = options.id;
                that.express_company = options.express;
                that.express_no = options.express_no;
            } else {
                uni.showToast({
                    title: response.msg,
                    icon: 'none',
                    duration: 1000
                });
            }
        }).catch(response => {
            that.$hideLoading();
            uni.showToast({
                title: response,
                icon: 'none',
                duration: 1000
            });
        });
    }
}
